@import "../../global";

.intro {
  background-color: white;
  display: flex;
  @include mobile{
    flex-direction: column;
    align-items: center;
  }

   .left{
     flex:0.5;
     overflow: hidden;

     .imgContainer{
       width: 700px;
       height: 700px;
       background-color: $secondaryColor;
       border-radius:50%;
       display: flex;
       align-items: flex-end;
       justify-content: center;
       float: right;

       @include mobile{
         align-items: flex-start;
       }

       img{
         height: 90%;

         @include mobile{
           height: 50%;
         }
       }
     }
   }
    .right{
      flex: 0.5;
      position: relative;

      .wrapper{
        width: 100%;
        height: 100%;
        padding-left: 50px;
        display: flex;
        flex-direction: column;
        justify-content: center;

        @include mobile{
          padding-left: 0;
          align-items: center;
        }

        h1{
          font-size: 60px;
          margin: 10px;

          @include mobile{
            font-size: 40px;
          }
        }
        h2{
          font-size: 35px;
        }
        h3{
          font-size: 30px;
          @include mobile{
            font-size: 20px;
          }
        }
        span {
           font-size: inherit;
           color: $secondaryColor;
         }
      }
      a{
        position: absolute;
        bottom: 10px;
        left: 40% ;
        font-size: 60px;
        animation: bounce 2s infinite;
        @keyframes bounce {
          0%, 20%, 50%, 80%, 100% {
            transform: translateY(0);
          }
          40% {
            transform: translateY(-30px);
          }
          60% {
            transform: translateY(-15px);
          }
        }
        @include mobile{
          display: none;
        }
      }
    }
}